import Vue from 'vue'
import App from './App'
import router from './router'
import VueClip from 'vue-clip'
import Paginate from 'vuejs-paginate'
import VeeValidate, { Validator } from 'vee-validate'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.component('paginate', Paginate);
Vue.use(VueClip);
Vue.use(ElementUI)
const dictionary = {
  zh_CN: {
    messages:{
      after: (field, [target]) => `该选项必须在${target}之后`,
      alpha_dash: (field) => `该选项能够包含字母数字字符，包括破折号、下划线`,
      alpha_num: (field) => `该选项只能包含字母数字字符.`,
      alpha_spaces: (field) => `该选项只能包含字母字符，包括空格.`,
      alpha: (field) => ` 该选项 只能包含字母字符.`,
      before: (field, [target]) => `该选项必须在${target}之前.`,
      between: (field, [min, max]) => `该选项 必须在${min} ${max}之间.`,
      confirmed: (field, [confirmedField]) => `该选项不能和${confirmedField}匹配.`,
      date_between: (field, [min, max]) => `该选项必须在${min}和${max}之间.`,
      date_format: (field, [format]) => `该选项必须在在${format}格式中.`,
      decimal: (field, [decimals] = ['*']) => `该选项 必须是数字的而且能够包含${decimals === '*' ? '' : decimals} 小数点.`,
      digits: (field, [length]) => `该选项必须是数字，且精确到 ${length}数`,
      dimensions: (field, [width, height]) => `该选项必须是 ${width} 像素到 ${height} 像素.`,
      email: (field) => `该选项必须是有效的邮箱.`,
      ext: (field) => `该选项必须是有效的文件.`,
      image: (field) => `该选项必须是图片.`,
      in: (field) => `该选项必须是一个有效值.`,
      ip: (field) => `该选项必须是一个有效的地址.`,
      max: (field, [length]) => `该选项不能大于${length}字符.`,
      mimes: (field) => `该选项必须是有效的文件类型.`,
      min: (field, [length]) => `该选项必须至少有 ${length} 字符.`,
      not_in: (field) => `该选项必须是一个有效值.`,
      numeric: (field) => `该选项只能包含数字字符.`,
      regex: (field) => `该选项格式无效.`,
      required: (field) => `该项不能为空.`,
      size: (field, [size]) => `该选项必须小于 ${size} KB.`,
      url: (field) => `该选项不是有效的url.`

    }
  }
};
Validator.updateDictionary(dictionary);
Vue.use(VeeValidate, {locale: "zh_CN"});


Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {App}
})



