            <a href="javascript:void(0)" v-if="file.mime == 'image'"><img src="../../../assets/images/hh2.jpg" width="94" height="94"></a>
            <a href="javascript:void(0)" v-else-if="file.mime == 'video'"><img src="../../../assets/images/hh1.jpg" width="94" height="94"></a>
            <a href="javascript:void(0)" v-else="file.mime == 'other'"><img src="../../../assets/images/hh3.jpg" width="94" height="94"></a>
          </dd>
          <dd class="gtv1"><input type="button" class="anniu7" value="编辑" data-toggle="modal" :data-target="'#modal_safe_' + index"></dd>
          <!-- 模态层 -->
          <div class="modal fade" :id="'modal_safe_' + index" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog" style="width: 1100px;">
              <div class="modal-content">
                <div class="modal-header">
                  <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                  <h4 class="modal-title">编辑</h4>
                </div>
                <div class="modal-body">
                  <div style="margin: 5px auto; width: 1000px;">
                    <textarea style="border: 1px solid #DDDDDD; width: 100%; height: 200px;" v-model="file.describe"></textarea>
                    <div>
                      <img :src="nginx + file.url" style="width: 100%; height: 300px; margin-top: 10px;" v-if="file.mime == 'image'"/>
                      <a :href="basePath + '/fit_task/download?fileUrl=' + file.url + '&oldFileName=' + file.oldFileName" target="_blank" v-else
                         @click="download(file.url)">{{file.oldFileName}} 点击下载</a>
                    </div>
                  </div>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                  <button type="button" class="btn btn-primary" @click="saveDescribe('safe', index)">提交更改</button>
                </div>
              </div>
            </div>
          </div>
        </dl>
      </div>
    </div>

    <div class="ertvct2">
      <dl class="iklnn">十、质量自检记录表<span class="font2">*</span></dl>
      <dl class="trfv22">
        <dd class="wrty">步骤详述</dd>
        <dd class="yuvvv"><textarea name="" cols="" rows="" class="uyop" v-model="fitStandard.selfcheckDetail"></textarea></dd>
      </dl>

      <div class="erfc">

        <vue-clip :options="options" :on-complete="completeSelf">
          <template slot="clip-uploader-action">
            <div>
              <div class="dz-message">
                <dl class="uyt">
                  <dd class="tvfr" style="margin:16px 0 0 0;">
                    <a href="javascript:void(0)">
                      <img src="../../../assets/images/hh4.jpg" width="94" height="94"></a>
                  </dd>
                  <dd class="gtv1">&nbsp;</dd>
                </dl>
              </div>
            </div>
          </template>
        </vue-clip>

        <dl class="uyt" v-for="(file, index) in fitStandard.selfcheckAffix">
          <dd class="ujh">
            <a href="javascript:void(0)" @click="removeSelf(index)">
              <img src="../../../assets/images/hh5.jpg" width="15" height="15">
            </a>
          </dd>
          <dd class="tvfr">
            <a href="javascript:void(0)" v-if="file.mime == 'image'"><img src="../../../assets/images/hh2.jpg" width="94" height="94"></a>
            <a href="javascript:void(0)" v-else-if="file.mime == 'video'"><img src="../../../assets/images/hh1.jpg" width="94" height="94"></a>
            <a href="javascript:void(0)" v-else="file.mime == 'other'"><img src="../../../assets/images/hh3.jpg" width="94" height="94"></a>
          </dd>
          <dd class="gtv1"><input type="button" class="anniu7" value="编辑" data-toggle="modal" :data-target="'#modal_self_' + index"></dd>
          <!-- 模态层 -->
          <div class="modal fade" :id="'modal_self_' + index" tabindex="-1" role="dialog" aria-hidden="true">
            <div class="modal-dialog" style="width: 1100px;">
              <div class="modal-content">
                <div class="modal-header">
                  <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                  <h4 class="modal-title">编辑</h4>
                </div>
                <div class="modal-body">
                  <div style="margin: 5px auto; width: 1000px;">
                    <textarea style="border: 1px solid #DDDDDD; width: 100%; height: 200px;" v-model="file.describe"></textarea>
                    <div>
                      <img :src="nginx + file.url" style="width: 100%; height: 300px; margin-top: 10px;" v-if="file.mime == 'image'"/>
                      <a :href="basePath + '/fit_task/download?fileUrl=' + file.url + '&oldFileName=' + file.oldFileName" target="_blank" v-else
                         @click="download(file.url)">{{file.oldFileName}} 点击下载</a>
                    </div>
                  </div>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                  <button type="button" class="btn btn-primary" @click="saveDescribe('self', index)">提交更改</button>
                </div>
              </div>
            </div>
          </div>
        </dl>
      </div>
    </div>


  </div>
</template>

<script>
  import ProcessFlow from './ProcessFlow.vue'
  import '../../../assets/js/bootstrap.min.js'
  export default {
    name: 'fitStandEdit',
    props: ['fitStandard'],
    data () {
      return {
        options: {
          url: basePath + '/fit_task/file_upload',
          paramName: 'file'
        },
        msg: 'process-flow',
        nginx: '',
        steps: [],
        basePath: basePath
      }
    }, components: {
      'process-flow': ProcessFlow
    }, methods: {
      add: function () {
        this.fitStandard.processFlow.push({title: '', describe: '', attachs: []});
        this.steps.push('process-flow');
      },
      delteStep: function (index) {
        this.steps.splice(index, 1);
        this.fitStandard.processFlow.splice(index, 1);
      }, completeRely (file, status, xhr) {
        this.fitStandard.relyStandardAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeRely (index) {
        this.fitStandard.relyStandardAffix.splice(index, 1);
      }, completeApp (file, status, xhr) {
        this.fitStandard.applicationRangeAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeApp (index) {
        this.fitStandard.applicationRangeAffix.splice(index, 1);
      }, completeCons (file, status, xhr) {
        this.fitStandard.constructionPrepareAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeCons (index) {
        this.fitStandard.constructionPrepareAffix.splice(index, 1);
      }, completeTask (file, status, xhr) {
        this.fitStandard.taskConditionAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeTask (index) {
        this.fitStandard.taskConditionAffix.splice(index, 1);
      }, completeQua (file, status, xhr) {
        this.fitStandard.qualityCheckAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeQua (index) {
        this.fitStandard.qualityCheckAffix.splice(index, 1);
      }, completeAtt (file, status, xhr) {
        this.fitStandard.attentionAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeAtt (index) {
        this.fitStandard.attentionAffix.splice(index, 1);
      }, completePro (file, status, xhr) {
        this.fitStandard.productProtectionAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removePro (index) {
        this.fitStandard.productProtectionAffix.splice(index, 1);
      }, completeSafe (file, status, xhr) {
        this.fitStandard.safetyGreenAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeSafe (index) {
        this.fitStandard.safetyGreenAffix.splice(index, 1);
      }, completeSelf (file, status, xhr) {
        this.fitStandard.selfcheckAffix.push(JSON.parse(JSON.parse(xhr.response)));
      }, removeSelf (index) {
        this.fitStandard.selfcheckAffix.splice(index, 1);
      }, saveDescribe(name, index) {
        $('#modal_' + name + '_' + index).modal('hide');
      }
    }, mounted() {
      var _this = this;
      $.ajax({
        url: basePath + '/fit_task/get_standard_by_id',
        type: 'post',
        dataType: 'json',
        data: {
          fitStandardId: this.$route.query.fitStandardId
        },
        success: function (result) {
          for (var key in result.data) {
            if (_this.fitStandard[key] instanceof Array) {
              _this.fitStandard[key] = JSON.parse(result.data[key]);
            } else {
              _this.fitStandard[key] = result.data[key];
            }
          }
          for (var i = 0; i < _this.fitStandard.processFlow.length; i++) {
            _this.steps.push('process-flow');
          }
          _this.nginx = result.nginx;
        }
      });
    }
  }

</script>



