<template>
  <div class="center">
    <div class="main1">
      <div class="zong">
        <dl class="uy7">
          <dd class="iolm">分类筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">等级筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">单价筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">供应区域筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <div v-for="(l, index) in list">
          <table border="1" class="aa35">
            <tr style="background:#f5f5f5;">
              <td width="150">分类</td>
              <td width="80">材料ID</td>
              <td width="120">材料名称</td>
              <td width="80">单位</td>
              <td width="80">规格型号</td>
              <td width="80">单价<br>（元）</td>
              <td width="450">供应区域</td>
            </tr>
            <tr v-for="(m, index) in l.materialVersions">
              <td :rowspan="l.materialVersions.length" v-if="index == 0">{{l.categoryName}}</td>
              <td :rowspan="l.materialVersions.length" v-if="index == 0">{{l.id}}</td>
              <td :rowspan="l.materialVersions.length" v-if="index == 0">{{l.title}}</td>
              <td :rowspan="l.materialVersions.length" v-if="index == 0">{{l.unit}}</td>
              <td>{{m.version}}</td>
              <td>{{m.unitPrice}}</td>
              <td>{{m.area}}</td>
            </tr>
          </table>
          <dl class="etrf5">
            <input type="button" class="anniu33" value="编辑"> <input type="button" class="anniu34" value="删除">
          </dl>
        </div>
        <dl class="ertvc"><input type="button" class="ybf18" value="新增材料定额"></dl>

      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'quotaWorker',
    data () {
      return {
        list: []
      }
    }, mounted() {
      var _this = this;
      axios({
        method: 'post',
        url: basePath + '/quota_material/list',
      }).then(function (response) {
        _this.list = response.data;
        console.info(_this.list);
      });
    }, methods: {}
  }
</script>



