<template>
  <div class="center">
    <div class="main">
      <div class="left">
        <div class="list">
          <ul class="yiji">
            <li><a href="javascript:void(0)" class="inactive" :class="{inactives:isOpen}" v-on:click="fold">基础装修</a>
              <ul :style="{display:ifDisplay}">
                <li>
                  <div v-on:click="foldPhase">
                    <router-link to="/siderBar/fitRuleIndex" class="inactive" :class="{inactives:isPhaseOpen}">装修规则
                    </router-link>
                  </div>
                  <ul :style="{display:ifPhaseDisplay}">
                    <li v-for="(phase,key,index) in ruleData">
                      <router-link :to="{ path: '/siderBar/fitPhase', query: { phase: key,version:ruleVersion }}">
                        第{{key}}阶段
                      </router-link>
                    </li>
                  </ul>
                </li>
              </ul>

              <ul :style="{display:ifDisplay}">
                <li>
                  <div v-on:click="foldQuote">
                    <router-link to="/siderBar/eng" class="inactive" :class="{inactives:isQuoteOpen}">工程定额</router-link>
                  </div>
                  <ul :style="{display:ifQuoteDisplay}">
                    <li>
                      <router-link to="/quota_worker">工人定额</router-link>
                    </li>
                    <li>
                      <router-link to="/quota_material">材料定额</router-link>
                    </li>
                    <li>
                      <router-link to="/quota_machine">机械定额</router-link>
                    </li>
                  </ul>
                </li>
              </ul>
            </li>
            <li><a href="#" class="inactive">家居软装</a></li>
            <li><a href="#" class="inactive">智能触控</a></li>
          </ul>
        </div>

      </div>
      <div id="right">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  import VM from '../../common/js/bus.js'

  export default{
    data(){
      return {
        ruleData: [],
        isOpen: false,
        ifDisplay: 'none',
        isQuoteOpen: false,
        ifQuoteDisplay: 'none',
        isPhaseOpen: false,
        ifPhaseDisplay: 'none',
        ruleVersion: ''
      }
    },
    mounted: function () {
      this.init()
    }
    ,
    methods: {
      fold(){
        if (this.isOpen == true) {
          this.isOpen = false,
            this.ifDisplay = 'none'
        }
        else {
          this.isOpen = true,
            this.ifDisplay = 'block'
        }
      },
      foldQuote(){
        this.init()
        if (this.isQuoteOpen == true) {
          this.isQuoteOpen = false,
            this.ifQuoteDisplay = 'none'
        }
        else {
          this.isQuoteOpen = true,
            this.ifQuoteDisplay = 'block'
        }
      },
      foldPhase(){
        if (this.isPhaseOpen == true) {
          this.isPhaseOpen = false,
            this.ifPhaseDisplay = 'none'
        }
        else {
          this.isPhaseOpen = true,
            this.ifPhaseDisplay = 'block'
        }
      },
      init(){
        var _this = this
        VM.bus.$on('sendRule', function (data) {
          _this.ruleData = data.fitTaskList
          _this.ruleVersion = data.fitRule.version
        })
      }
    }

  }
</script>




