<template>
<div class="center">
 <div class="main1">
  <div class="zong">
   <dl class="tgvn"><dd class="ery">编辑装修模板</dd></dl>
   <div class="tgr">
    <div class="rtvmm">
     <div class="ygf">

     <div class="ertw">
      <dl class="trfv">
       <dd class="wrty">模板描述<span class="font2">*</span></dd>
       <dd class="yuvvv"><input type="text" class="bgt" value="请填写" v-model="description"></dd>
      </dl>
     </div>

     <div class="ertw">
      <dl class="trfv">
       <dd class="wrty">适用城市<span class="font2">*</span></dd>
       <dd class="yuvvv">
        <select name="" class="bgt1" v-model="currCity">
        <option v-for="city in cityList" :value="city">
                 {{city}}
         </option>
         </select>
         </dd>
      </dl>
     </div>

     </div>

     <div class="ertc">
     <dl class="erfbb">
       <dd class="rtfv">版本号<span class="font2">*</span></dd>
       <dd class="rtffd"><input name="" type="text" class="yt6b" v-model="version"></dd>
      </dl>
     </div>


     <div class="wrtvn">
      <dl class="ertvb">已选择：</dl>
      <div :style="{display:isCityDisplay}">
      <dl class="ung" >{{currCity}}</dl>
      <dl class="ertvbm">
        <a href="javascript:void(0)" @click="delCity"><img src="../../../assets/images/cha.jpg" width="22" height="22"></a>
      </dl>
      </div>
     </div>

    </div>


    <div class="rtvmm2" style=" border-bottom:none;">
     <div class="ygf22">

     <div class="ertw">
      <dl class="trfv">

       <dd class="wrty">选择施工任务<span class="font2">*</span></dd>
        <dd class="yuvvv">
          <select name="selectedVersion" class="bgt1" v-model="selectedVersion" @change="changeVersion()" style="width:100px;">
            <option value="">选择版本号</option>
            <option v-for="key in selectedKeys" :value="key">{{key}}</option>
          </select>
        </dd>
        <dd class="yuvvv">
          <select name="selectedTaskItem" class="bgt1" style="width:300px;" v-model="selectedTaskItem" @change="changeVersionTask()">
            <option :value="{}">选择施工任务</option>
            <option v-for="item in selectedVersionTasks" :value="item">{{item.name}}</option>
          </select>
        </dd>
      </dl>
     </div>

     <div class="wrtvn">
      <dl class="ertvb">已选择：</dl>
      <div v-for="(task,index) in selectedTaskList">
      <dl class="yubg">
       <dd class="ung">{{task.version}}|{{task.name}}</dd>
       <dd class="ertvbm">
        <a href="javascript:void(0)"  @click="delTask(index)"><img src="../../../assets/images/cha.jpg" width="22" height="22"></a>
       </dd>
      </dl>
      </div>
     </div>


     <dl class="ungjk"><input @click="save" type="button" class="lomn" value="保存"></dl>
     <dl class="ungjk"><input type="button" class="lomn1" value="立即编辑模板" @click="addTask"></dl>

     </div>
     <div class="wrtvn1"></div>

    </div>
   </div></div>
		<div class="vcon" style="display: none;">
		<ul class="vconlist clearfix">
		 <li class="select">
          <dl class="u8ik">
           <dd>1、调取步骤一标题</dd>
           <dd>步骤一描述</dd>
           <dd>2、调取步骤二标题</dd>
           <dd>步骤一描述</dd>
          </dl>
         </li>
		</ul>
	</div>
</div>
   </div>
</template>
<script>
import axios from 'axios'
  export default{
     data () {
      return{
        fitRuleId:'',
        ruleData:'',
        description:'',
        cityList:["北京","上海","广州","深圳"],
        currCity: '',
        version:'',
        task:'',
        isCityDisplay:'none',
        taskList:[],
        selectedTask:'',
        selectedTaskList:[],
        delNum:'',
        taskIdList:'',
        flag:'',
        selectedVersion: "", //选择的模板版本号
        tasksMap: {}, //初始化的任务列表，按版本号归类
        selectedKeys: [],
        selectedVersionTasks: [], //
        selectedTaskItem: {}
      }
    },
    mounted:
        function(){
          this.getTaskList();
            this.init()
        }
    ,
   methods: {
     addTask() {
       this.$router.push({ path: '/add_fit_task', query: {version: this.version}});
     },
       init(){
       this.version=this.$route.params.version
         var url=basePath+"/fitRule/version/"+this.version;
         axios.get(url).then(response=>(
         this.ruleData=response.data.fitRule,
         this.description=this.ruleData.description,
         this.currCity=this.ruleData.city,
         this.isCityDisplay='block',
           this.fitRuleId=this.ruleData.id
       ))
       },
     delCity(){
     this.isCityDisplay='none'
      this.taskIdList=''
      this.selectedTaskList.forEach(element=>{
      this.taskIdList+=element.value+','
      })

    },
    delTask(num){
     this.selectedTaskList.splice(num,1)
        this.taskIdList=''
      this.selectedTaskList.forEach(element=>{
      this.taskIdList+=element.value+','
      })
    },
    save(){
      this.selectedTaskList.forEach(element => {
        this.taskIdList += element.id + ','
      })
        var url=basePath+"/fitRule/one";
     axios.put(url,{
         id:this.fitRuleId,
       city:this.currCity,
       task:this.taskIdList,
       version:this.version,
       description:this.description,
     }
      ).then(response=>(
         alert(response.data.msg),
         this.$router.push({path:"/siderBar/fitRuleIndex"})
       ))
    },
    getTaskList() {
      let taskList = [];
      let url = basePath + "/fit_task/all"
      axios.get(url).then(
        response => {
          console.log(response);
          if (response.data) {
            for (let task of response.data) {
              taskList.push({
                id: task.id,
                name: task.title,
                version: task.version
              });
            }
            console.log(taskList);

            if (taskList && taskList.length > 0) {
              for (let task of taskList) {
                if (!this.tasksMap[task.version]) {
                  this.selectedKeys.push(task.version);
                  this.tasksMap[task.version] = [];
                }
                this.tasksMap[task.version].push(task);
              }
            }
            console.log(this.tasksMap);

          }
        }, error => {
          console.log(error);
        }
      );



    },
    changeVersion() {
      console.log("changeVersion");
      console.log(this.selectedVersion);
      if (this.selectedVersion == "") {
        this.selectedVersionTasks = [];
      } else {
        this.selectedVersionTasks = this.tasksMap[this.selectedVersion];
      }
      this.selectedTaskItem = {};

    },
    changeVersionTask() {
      console.log("changeVersionTask");
      //把选择的任务加入到选择任务列表中
      if (this.selectedTaskItem.id) {
        if (this.selectedTaskList.length == 0) {
          this.selectedTaskList.push(this.selectedTaskItem);
          return;
        }
        let existed = false;
        for (let task of this.selectedTaskList) {
          if (task.id == this.selectedTaskItem.id) {
            // console.log("existed");
            existed = true;
            break;
          }
        }
        if (!existed) {
          this.selectedTaskList.push(this.selectedTaskItem);
        }
      }

    }},
  watch:{
    currCity:function(city){
      this.isCityDisplay='block'
    }
  }
  }
</script>



