<template>
  <div class="center">
    <div class="main1">
      <div class="zong">
        <dl class="rty"><input type="button" class="yt6" value="保存" @click="save"></dl>
        <dl class="tgvn">
          <dd class="ery">新增施工规范</dd>

          dsadas
        </dl>
        <div class="tgr">
          <fit-task-edit :showTables="showTables" :fitTask="fitTask"></fit-task-edit>
          <fit-stand-add :fitStandard="fitStandard"></fit-stand-add>
          <dl class="ertvc"><input type="button" class="ybf199" value="保存" @click="save"></dl>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import FitTaskEdit from './FitTaskEdit.vue'
  import FitStandAdd from './FitStandAdd.vue'
  import axios from 'axios'
  export default {
    name: 'baseFitTaskEdit',
    data () {
      return {
        showTables: {
          wor: [],
          mat: [],
          mac: []
        },
        fitTask: {
          id: '',
          title: '',
          category: '',
          worker: '',
          material: '',
          machine: '',
          phase: '',
          version: '',
          sequence: '',
          newPhaseFlag: false
        },
        fitStandard: {
          processFlow: [{
            title: '',
            describe: '',
            attachs: []
          }],
          relyStandardDetail: '',
          relyStandardAffix: [],
          applicationRangeDetail: '',
          applicationRangeAffix: [],
          constructionPrepareDetail: '',
          constructionPrepareAffix: [],
          taskConditionDetail: '',
          taskConditionAffix: [],
          qualityCheckDetail: '',
          qualityCheckAffix: [],
          attentionDetail: '',
          attentionAffix: [],
          productProtectionDetail: '',
          productProtectionAffix: [],
          safetyGreenDetail: '',
          safetyGreenAffix: [],
          selfcheckDetail: '',
          selfcheckAffix: []
        }
      }
    }, components: {
      'fit-stand-add': FitStandAdd,
      'fit-task-edit': FitTaskEdit
    }, methods: {
      save: function () {
        for (var i = 0; i < this.showTables.wor.length; i++) {
          if (i != 0) {
            this.fitTask.worker += ',';
          }
          this.fitTask.worker += this.showTables.wor[i].id;
        }
        for (var i = 0; i < this.showTables.mat.length; i++) {
          if (i != 0) {
            this.fitTask.material += ',';
          }
          this.fitTask.material += this.showTables.mat[i].id;
        }
        for (var i = 0; i < this.showTables.mac.length; i++) {
          if (i != 0) {
            this.fitTask.machine += ',';
          }
          this.fitTask.machine += this.showTables.mac[i].id;
        }
        var _this = this;
        $.ajax({
          url: basePath + '/fit_task/edit',
          type: 'post',
          dataType: 'json',
          data: {
            'fitTask.title': _this.fitTask.title,
            'fitTask.category': _this.fitTask.category,
            'fitTask.worker': _this.fitTask.worker,
            'fitTask.material': _this.fitTask.material,
            'fitTask.machine': _this.fitTask.machine,
            'fitTask.phase': _this.fitTask.phase,
            'fitTask.version': _this.fitTask.version,
            'fitTask.sequence': _this.fitTask.sequence,
            processFlow: JSON.stringify(_this.fitStandard.processFlow),
            relyStandardDetail: _this.fitStandard.relyStandardDetail,
            relyStandardAffix: JSON.stringify(_this.fitStandard.relyStandardAffix),
            applicationRangeDetail: _this.fitStandard.applicationRangeDetail,
            applicationRangeAffix: JSON.stringify(_this.fitStandard.applicationRangeAffix),
            constructionPrepareDetail: _this.fitStandard.constructionPrepareDetail,
            constructionPrepareAffix: JSON.stringify(_this.fitStandard.constructionPrepareAffix),
            taskConditionDetail: _this.fitStandard.taskConditionDetail,
            taskConditionAffix: JSON.stringify(_this.fitStandard.taskConditionAffix),
            qualityCheckDetail: _this.fitStandard.qualityCheckDetail,
            qualityCheckAffix: JSON.stringify(_this.fitStandard.qualityCheckAffix),
            attentionDetail: _this.fitStandard.attentionDetail,
            attentionAffix: JSON.stringify(_this.fitStandard.attentionAffix),
            productProtectionDetail: _this.fitStandard.productProtectionDetail,
            productProtectionAffix: JSON.stringify(_this.fitStandard.productProtectionAffix),
            safetyGreenDetail: _this.fitStandard.safetyGreenDetail,
            safetyGreenAffix: JSON.stringify(_this.fitStandard.safetyGreenAffix),
            selfcheckDetail: _this.fitStandard.selfcheckDetail,
            selfcheckAffix: JSON.stringify(_this.fitStandard.selfcheckAffix)
          },
          success: function (result) {
            _this.$router.push({path: '/siderBar/fitRuleIndex'});
          }
        });
      }
    }
  }
</script>



