import Vue from 'vue'
import Router from 'vue-router'
import Eng from '../components/quota/Eng.vue'
import Header from '../../common/components/Header.vue'
import FitRuleIndex from '../components/fitRule/FitRuleIndex.vue'
import FitRuleBlank from '../components/fitRule/FitRuleBlank.vue'
import SiderBar from '../components/SiderBar.vue'
import FitRuleAdd from '../components/fitRule/FitRuleAdd.vue'
import FitRuleEdit from '../components/fitRule/FitRuleEdit.vue'
import BaseFitTaskAdd from '../components/fitTask/BaseFitTaskAdd.vue'
import BaseFitTaskEdit from '../components/fitTask/BaseFitTaskEdit.vue'
import QuotaMaterial from '../components/quota/QuotaMaterial.vue'
import FitPhase from '../components/fitRule/FitPhase.vue'
import FitStandardInfo from "../components/fitTask/FitStandardInfo.vue"
import QuotaMachine from '../components/quota/QuotaMachine.vue'
import QuotaWorker from '../components/quota/QuotaWorker.vue'
import BaseFitStandard from "../components/fitTask/BaseFitStandard.vue"
import QuotaMachineAdd from "../components/quota/QuotaMachineAdd.vue"
import QuotaMachineEdit from "../components/quota/QuotaMachineEdit.vue"
import QuotaWorkerAdd from "../components/quota/QuotaWorkerAdd.vue"
import QuotaWorkerEdit from "../components/quota/QuotaWorkerEdit.vue"
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Header',
      component: Header,
      children: [
        {
          path: 'siderBar',
          name: 'siderBar',
          component: SiderBar,
          children: [
            {
              path: 'fitRuleIndex',
              name: 'fitRuleIndex',
              component: FitRuleIndex
            },
            {
              path: 'fitRuleBlank',
              name: 'fitRuleBlank',
              component: FitRuleBlank
            },
            {
              path: 'eng',
              name: 'Eng',
              component: Eng
            },
            {
              path: 'fitPhase',
              name: 'fitPhase',
              component: FitPhase
            }
          ]
        },
        {
          path: 'fitRuleAdd',
          name: 'fitRuleAdd',
          component: FitRuleAdd
        },
        {
          path: '/fitRuleEdit/:version',
          name: 'fitRuleEdit',
          component: FitRuleEdit
        },
        {
          path: '/add_fit_task',
          name: 'BaseFitTaskAdd',
          component: BaseFitTaskAdd
        }, {
          path: '/edit_fit_task',
          name: 'BaseFitTaskEdit',
          component: BaseFitTaskEdit
        },
        {
          path: '/quota_material',
          name: 'QuotaMaterial',
          component: QuotaMaterial
        },
        {
          path: '/quota_machine',
          name: 'QuotaMachine',
          component: QuotaMachine
        },
        {
          path: '/quota_worker',
          name: 'QuotaWorker',
          component: QuotaWorker
        },
        {
          path: "/fitStandardInfo/:fitStandardId",
          name: "fitStandardInfo",
          component: FitStandardInfo
        },
        {
          path: "/add_fit_standard",
          name: "BaseFitStandard",
          component: BaseFitStandard
        },
        {
          path: "/add_quota_machine",
          name: "QuotaMachineAdd",
          component: QuotaMachineAdd
        },
        {
          path: "/edit_quota_machine/:id",
          name: "QuotaMachineEdit",
          component: QuotaMachineEdit
        },
        {
          path: "/add_quota_worker",
          name: "QuotaWorkerAdd",
          component: QuotaWorkerAdd
        },
        {
          path: "/edit_quota_worker/:id",
          name: "QuotaWorkerEdit",
          component: QuotaWorkerEdit
        }
      ]
    }
  ]
})



