<template>
  <div>

    <!-- 槽口 -->
    <slot></slot>

    <dl class="trfv22">
      <dd class="wrty">步骤标题</dd>
      <dd class="yuvvv"><input type="text" class="bgt" v-model="processFlow[index].title"></dd>
    </dl>

    <dl class="trfv22">
      <dd class="wrty">步骤详述</dd>
      <dd class="yuvvv"><textarea name="" cols="" rows="" class="uyop" v-model="processFlow[index].describe"></textarea></dd>
    </dl>

    <div class="erfc">


      <vue-clip :options="options" :on-complete="complete">
        <template slot="clip-uploader-action">
          <div>
            <div class="dz-message">
              <dl class="uyt">
                <dd class="tvfr" style="margin:16px 0 0 0;">
                  <a href="javascript:void(0)"><img src="../../../assets/images/hh4.jpg" width="94" height="94"></a>
                </dd>
                <dd class="gtv1">&nbsp;</dd>
              </dl>
            </div>
          </div>
        </template>
      </vue-clip>

      <dl class="uyt" v-for="(file, i) in processFlow[index].attachs">
        <dd class="ujh">
          <a href="javascript:void(0)" @click="remove(i)">
            <img src="../../../assets/images/hh5.jpg" width="15" height="15">
          </a>
        </dd>
        <dd class="tvfr">
          <a href="javascript:void(0)" v-if="file.mime == 'image'"><img src="../../../assets/images/hh2.jpg" width="94" height="94"></a>
          <a href="javascript:void(0)" v-else-if="file.mime == 'video'"><img src="../../../assets/images/hh1.jpg" width="94" height="94"></a>
          <a href="javascript:void(0)" v-else="file.mime == 'other'"><img src="../../../assets/images/hh3.jpg" width="94" height="94"></a>
        </dd>
        <dd class="gtv1"><input type="button" class="anniu7" value="编辑" data-toggle="modal" :data-target="'#modal_' + index + '_' + i"></dd>

        <!-- 模态层 -->
        <div class="modal fade" :id="'modal_' + index + '_' + i" tabindex="-1" role="dialog" aria-hidden="true">
          <div class="modal-dialog" style="width: 1100px;">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                <h4 class="modal-title">编辑</h4>
              </div>
              <div class="modal-body">
                <div style="margin: 5px auto; width: 1000px;">
                  <textarea style="border: 1px solid #DDDDDD; width: 100%; height: 200px;" v-model="file.describe"></textarea>
                  <div>
                    <img :src="nginx + file.url" style="width: 100%; height: 300px; margin-top: 10px;" v-if="file.mime == 'image'"/>
                    <a :href="basePath + '/fit_task/download?fileUrl=' + file.url + '&oldFileName=' + file.oldFileName" target="_blank" v-else @click="download(file.url)">{{file.oldFileName}} 点击下载</a>
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                <button type="button" class="btn btn-primary" @click="saveDescribe(index, i)">提交更改</button>
              </div>
            </div>
          </div>
        </div>
      </dl>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['processFlow', 'index', 'nginx'],
    data () {
      return {
        options: {
          url: basePath + '/fit_task/file_upload',
          paramName: 'file'
        },
        basePath: basePath
      }
    }, methods: {
      complete (file, status, xhr) {
        this.processFlow[this.index].attachs.push(JSON.parse(JSON.parse(xhr.response)))
      }, remove (i) {
        this.$children[0].$data.files.splice(i, 1);
        this.processFlow[this.index].attachs.splice(i, 1);
      }, saveDescribe(index, i) {
        $('#modal_' + index + '_' + i).modal('hide');
      }, download(url) {
          console.info(url);
      }
    }

  }
</script>



