<template>
  <div class="center">
    <div class="main1">
      <div class="zong">
        <dl class="rty"><input type="button" class="yt6" value="保存" @click="save"></dl>
        <dl class="tgvn">
          <dd class="ery">添加机械定额</dd>
        </dl>

        <div class="tgr">

          <div class="rtvmm">
            <div class="ygf">

              <div class="ertw">
                <dl class="trfv">
                  <dd class="wrty">机械名称<span class="font2">*</span></dd>
                  <dd class="yuvvv"><input type="text" class="bgt" name="title" v-validate="'required'" v-model="quotaWorker.title"></dd>
                </dl>
                <span v-show="errors.has('title')" style="margin-left: 105px; color: red; font-size: xx-small">{{ errors.first('title') }}</span>
              </div>

              <div class="ertw">
                <dl class="trfv">
                  <dd class="wrty">机械分类<span class="font2">*</span></dd>
                  <dd class="yuvvv">
                    <select class="bgt1" v-model="quotaWorker.categoryId">
                      <option value="1">木工</option>
                      <option value="2">泥工</option>
                      <option value="3">焊工</option>
                      <option value="4">瓦工</option>
                      <option value="5">钢筋工</option>
                      <option value="6">架子工</option>
                      <option value="7">油漆工</option>
                    </select>
                  </dd>
                </dl>
              </div>

              <div class="ertw">
                <dl class="trfv">
                  <dd class="wrty">单      位<span class="font2">*</span></dd>
                  <dd class="yuvvv"><input type="text" class="bgt11" name="unit" v-validate="'required'" v-model="quotaWorker.unit"></dd>
                </dl>
                <span v-show="errors.has('unit')" style="margin-left: 105px; color: red; font-size: xx-small">{{ errors.first('unit') }}</span>
              </div>
            </div>
          </div>
          <template v-for="(item, index) in versions">
            <comment :names="names" :is="item" :versions="quotaWorker.versions" :index="index">
              <input type="button" class="anniu35" value="删除" @click="remove(index)" v-if="quotaWorker.versions.length != 1">
            </comment>
          </template>

          <!-- modal -->
          <vue-msg ref="msg"></vue-msg>

          <dl class="ertvc"><input type="button" class="ybf1998" value="添加规格型号" @click="add"></dl>
          <dl class="ertvc"><input type="button" class="ybf199" value="保存" @click="save"></dl>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Version from './Version.vue'
  import VueMsg from '../../../common/components/VueMsg.vue'
  import VM from '../../../common/js/bus.js'
  export default {
    name: 'quotaWorkerEdit',
    data () {
      return {
        names: {
          version: '型号',
          unitPrice: '损耗单价',
          area: '供应区域'
        },
        versions: [],
        quotaWorker: {
          id: '',
          title: '',
          categoryId: 1,
          unit: '',
          versions: [{version: '', area: [], unitPrice: ''}]
        },
        msg: ''
      }
    }, methods: {
      add() {
        this.versions.push('version');
        this.quotaWorker.versions.push({version: '', area: [], unitPrice: ''});
      }, remove(index) {
        this.versions.splice(index, 1);
        this.quotaWorker.versions.splice(index, 1);
      }, save() {
        // 验证当前组件
        this.$validator.validateAll().then(() => {
        }).catch(() => {
        });

        // 验证子组件
        VM.bus.$emit('validate');

        // 验证通过, 提交表单
        if (!this.errors.any()) {
          $.ajax({
            url: basePath + '/quota_worker/worker',
            type: 'post',
            dataType: 'json',
            data: {
              _method: 'put',
              id: this.quotaWorker.id,
              categoryId: this.quotaWorker.categoryId,
              title: this.quotaWorker.title,
              unit: this.quotaWorker.unit,
              versions: JSON.stringify(this.quotaWorker.versions)
            },
            success: (result) => {
              if (result.success) {
                this.$router.push({path: '/quota_worker'});
              } else {
                this.$refs.msg.show(result.msg);
              }
            }
          });
        }
      }
    }, components: {
      'version': Version,
      'vue-msg': VueMsg
    }, created() {
      $.ajax({
        url: basePath + '/quota_worker/worker/' + this.$route.params.id,
        type: 'get',
        dataType: 'json',
        success: (result) => {
          for (var i = 0; i < result.workerVersions.length; i++) {
            result.workerVersions[i].area = JSON.parse(result.workerVersions[i].area);
            this.versions.push('version');
          }
          this.quotaWorker.id = result.id;
          this.quotaWorker.title = result.title;
          this.quotaWorker.categoryId = result.categoryId;
          this.quotaWorker.unit = result.unit;
          this.quotaWorker.versions = result.workerVersions;
        }
      });
      VM.bus.$on('errors-changed', (errors) => {
        errors.forEach(e => {
          this.errors.add(e.field, e.msg, e.rule, e.scope);
        });
      });
    }
  }
</script>



