<template>
  <div>

    <div class="r-main">
      <div class="fgb">
        <dl class="ujb">
          <dd class="unbv">装修模板</dd>
          <dd class="pil"><select name="" class="ikm">
            <option>V1.1</option>
            <option>V1.2</option>
            <option>V1.3</option>
          </select></dd>
        </dl>
        <dl class="ujb">
          <dd class="unbv">适用城市</dd>
          <dd class="pil">
            <div class="demo">
              <!-- 在输入框加入id -->
              <input type="text" class="cityinput" id="citySelect" placeholder="请输入目的地">
            </div>
          </dd>
          <dd class="rtg"><input type="button" class="anniu" value="编辑"/></dd>
          <dd class="gyhb1"><input type="button" class="umk" value="删除当前模板"/></dd>
          <dd class="gyhb1"><input type="button" class="umk1" value="新增装修模板"/></dd>
        </dl>
      </div>
    </div>


    <div class="dtg">
      <dl class="tyv">工程定额</dl>
      <div class="fxdc">
        <div v-for="(task, index) in list">
          <table border="1" class="aa3">
            <tr>
              <td width="5%" bgcolor="#f5f5f5"><a href="javascript:void(0)">编号</a></td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">施工任务名称</a></td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">分类</a></td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">人工种类</a></td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">人工用时<br/></a>（h）</td>
              <td width="5%" bgcolor="#f5f5f5"><a href="javascript:void(0)">人工费<br/></a>（h）</td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">材料种类</a></td>
              <!--<td width="9%" bgcolor="#f5f5f5"><a href="javascript:void(0)">材料规格</a></td>
              <td width="9%" bgcolor="#f5f5f5"><a href="javascript:void(0)">材料用量</a></td>
              <td width="7%" bgcolor="#f5f5f5"><a href="javascript:void(0)">材料费<br/>（元）</a></td>-->
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">机械种类</a></td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">机械费</a><br/>（元）</td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">面积</a><br>（平米）</td>
              <td width="10%" bgcolor="#f5f5f5"><a href="javascript:void(0)">限时区间</a>（天）</td>
            </tr>
            <tr>
              <td>{{index + 1}}</td>
              <td><dl class="ert"><a href="javascript:void(0)">{{task.title}}</a></dl></td>
              <td>{{task.category}}</td>
              <td>
                <template v-for="(item, index) in task.listFitTaskWorker" track-by="index">
                  {{item.categoryName}}<hr style="margin: 0" v-if="index != task.listFitTaskWorker.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskWorker" track-by="index">
                  {{item.unitTaskTime}}<hr style="margin: 0" v-if="index != task.listFitTaskWorker.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskWorker" track-by="index">
                  {{item.cost}}<hr style="margin: 0" v-if="index != task.listFitTaskWorker.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskMaterial" track-by="index">
                  {{item.categoryName}}<hr style="margin: 0" v-if="index != task.listFitTaskMaterial.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskMachine" track-by="index">
                  {{item.categoryName}}<hr style="margin: 0" v-if="index != task.listFitTaskMachine.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskMachine" track-by="index">
                  {{item.unitPrice}}<hr style="margin: 0" v-if="index != task.listFitTaskMachine.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskArea" track-by="index">
                  {{item.area}}<hr style="margin: 0" v-if="index != task.listFitTaskArea.length -1">
                </template>
              </td>
              <td>
                <template v-for="(item, index) in task.listFitTaskArea" track-by="index">
                  【{{item.lowerLimit}}-{{item.upperLimit}}】<hr style="margin: 0" v-if="index != task.listFitTaskArea.length -1">
                </template>
              </td>
            </tr>
          </table>
          <dl class="erb">
            <input type="button" class="anniu33" value="编辑"/>
            <input type="button" class="anniu34" value="删除"/>
          </dl>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'eng',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        list: []
      }
    }, mounted() {
      var _this = this;
      axios.post(basePath + '/fit_task/list').then(function (response) {
        _this.list = response.data;
        console.info(_this.list);
      });
    }, methods: {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .demo input {
    width: 80px;
    height: 24px;
    margin: 0 0 0 10px;
  }
</style>



