<script>
  export default {
    name: 'fitTaskAdd',
    props: ['fitTask', 'showTables'],
    data () {
      return {
        listMachine: [],
        macVal: -1,
        listMachineVersion: [],
        macVersionVal: '',
        listMaterial: [],
        matVal: -1,
        listMaterialVersion: [],
        matVersionVal: '',
        listWorker: [],
        worVal: -1,
        listWorkerVersion: [],
        worVersionVal: '',
        listPhase: [],
        listSequence: [],
        listArea: [],
        areaVal: -1,
        listMeasure: [],
        measureVal: -1
      }
    }, methods: {
      remove(key1, key2, index) {// 移除表格某一行
        this.showTables[key1].splice(index, 1);
        console.info(key2)
        this[key2] = -1;
      }, getSequence() {// 获取次序
        var _this = this;
        _this.listSequence = [];
        $.ajax({
          url: basePath + '/fit_task/get_sequence',
          type: 'post',
          dataType: 'json',
          async: false,
          data: {
            phase: _this.fitTask.phase,
            version: _this.fitTask.version
          },
          success: function (result) {
            if (result.length == 0) {
              _this.listSequence.push(1);
            } else {
              _this.listSequence = result;
              _this.listSequence.push(_this.listSequence[_this.listSequence.length - 1] + 1);
            }
          }
        });
        _this.fitTask.sequence = _this.listSequence[_this.listSequence.length - 1];// 默认勾选最后一个次序
      }, valateTable(table, value) {// 验证表格
        var flag = true;
        for (var i = 0; i < table.length; i++) {
          if (table[i].id == value) {
            flag = false;
            break;
          }
        }
        return flag;
      }, showMacSelect() {
        this.macVersionVal = -1;// 右侧默认选中第一个
        if (this.macVal == -1) {
          this.listMachineVersion = [];// 清空右侧下拉
          this.showTables.mac = [];// 清空已选择
        } else {
          this.listMachineVersion = this.listMachine[this.macVal].machineVersions;
        }
      }, checkMac() {
        if (this.macVersionVal != -1) {
          var flag = this.valateTable(this.showTables.mac, this.listMachineVersion[this.macVersionVal].id);// 验证重复选择
          if (flag) {
            var temp = {
              categoryId: this.listMachine[this.macVal].categoryId,
              unit: this.listMachine[this.macVal].unit,
              title: this.listMachine[this.macVal].title,
              version: this.listMachineVersion[this.macVersionVal].version,
              unitPrice: this.listMachineVersion[this.macVersionVal].unitPrice,
              area: this.listMachineVersion[this.macVersionVal].area,
              id: this.listMachineVersion[this.macVersionVal].id
            };
            this.showTables.mac.push(temp);
          }
        }
      }, showMatSelect() {
        this.matVersionVal = -1;// 右侧默认选中第一个
        if (this.matVal == -1) {
          this.listMaterialVersion = [];// 清空右侧下拉
          this.showTables.mat = [];// 清空已选择
        } else {
          this.listMaterialVersion = this.listMaterial[this.matVal].materialVersions;
        }
      }, checkMat() {
        if (this.matVersionVal != -1) {
          var flag = this.valateTable(this.showTables.mat, this.listMaterialVersion[this.matVersionVal].id);// 验证重复选择
          if (flag) {
            var temp = {
              categoryId: this.listMaterial[this.matVal].categoryId,
              unit: this.listMaterial[this.matVal].unit,
              title: this.listMaterial[this.matVal].title,
              version: this.listMaterialVersion[this.matVersionVal].version,
              unitPrice: this.listMaterialVersion[this.matVersionVal].unitPrice,
              area: this.listMaterialVersion[this.matVersionVal].area,
              id: this.listMaterialVersion[this.matVersionVal].id
            };
            this.showTables.mat.push(temp);
          }
        }
      }, showWorSelect() {
        this.worVersionVal = -1;// 右侧默认选中第一个
        if (this.worVal == -1) {
          this.listWorkerVersion = [];// 清空右侧下拉
          this.showTables.wor = [];// 清空已选择
        } else {
          this.listWorkerVersion = this.listWorker[this.worVal].workerVersions;
        }
      }, checkWor() {
        if (this.worVersionVal != -1) {
          var flag = this.valateTable(this.showTables.wor, this.listWorkerVersion[this.worVersionVal].id);// 验证重复选择
          if (flag) {
            var temp = {
              categoryId: this.listWorker[this.worVal].categoryId,
              unit: this.listWorker[this.worVal].unit,
              title: this.listWorker[this.worVal].title,
              version: this.listWorkerVersion[this.worVersionVal].version,
              unitPrice: this.listWorkerVersion[this.worVersionVal].unitPrice,
              area: this.listWorkerVersion[this.worVersionVal].area,
              id: this.listWorkerVersion[this.worVersionVal].id,
              unitTaskTime: 0.00,
              cost: 0.00
            };
            this.showTables.wor.push(temp);
          }
        }
      }, checkArea() {
        if (this.areaVal != -1) {
          var flag = this.valateTable(this.showTables.area, this.listArea[this.areaVal].id);// 验证重复选择
          if (flag) {
            var temp = {
              id: this.listArea[this.areaVal].id,
              area: this.listArea[this.areaVal].area,
              upperLimit: this.listArea[this.areaVal].upperLimit,
              lowerLimit: this.listArea[this.areaVal].lowerLimit
            };
            this.showTables.area.push(temp);
          }
        } else {
          this.showTables.area = [];
        }
      }, checkMeasure() {
        if (this.measureVal != -1) {
          var flag = this.valateTable(this.showTables.measure, this.listMeasure[this.measureVal].id);// 验证重复选择
          if (flag) {
            var temp = {
              id: this.listMeasure[this.measureVal].id,
              name: this.listMeasure[this.measureVal].name
            };
            this.showTables.measure.push(temp);
          }
        } else {
          this.showTables.area = [];
        }
      }, calc(index) {
        this.showTables.wor[index].cost = this.showTables.wor[index].unitPrice * this.showTables.wor[index].unitTaskTime;
      }
    }, created () {
      var _this = this;
      _this.fitTask.version = _this.$route.query.version;
      if (_this.$route.query.phase) {// 如果在某阶段下面新增施工任务, 则阶段默认为该阶段
        _this.fitTask.phase = _this.$route.query.phase;
      }
      $.ajax({
        url: basePath + '/fit_task/get_eng',
        type: 'post',
        dataType: 'json',
        async: false,
        data: {
          version: _this.fitTask.version
        },
        success: function (result) {
          _this.listMachine = result.listMachine;
          _this.listMaterial = result.listMaterial;
          _this.listWorker = result.listWorker;
          _this.listArea = result.listArea;
          _this.listMeasure = result.listMeasure;
          if (result.listPhase.length == 0) {// 如果没有阶段, 阶段则为 1
            _this.listPhase.push(1);
          } else {
            _this.listPhase = result.listPhase;
            _this.listPhase.push(_this.listPhase[_this.listPhase.length - 1] + 1);// 可选阶段为当前阶段最大数 + 1
          }
          if (!_this.fitTask.phase) {
            _this.fitTask.phase = _this.listPhase[_this.listPhase.length - 1];// 如果阶段号为空, 则默认勾选最后一个阶段
          }
          _this.getSequence();// 根据阶段获取次序
        }
      });
    }
  }

</script>



