<template>
  <div class="center">
    <div class="main1">
      <div class="zong">
        <dl class="uy7">
          <dd class="iolm">分类筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">等级筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">单价筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <dl class="uy7">
          <dd class="iolm">供应区域筛选</dd>
          <dd class="ewr"><select name="" class="olp">
            <option>请选择</option>
          </select></dd>
        </dl>

        <div v-for="(l, index) in list">
          <table border="1" class="aa35">
            <tr style="background:#f5f5f5;">
              <td width="150">分类</td>
              <td width="80">工种ID</td>
              <td width="120">工种名称</td>
              <td width="80">单位</td>
              <td width="80">等级</td>
              <td width="80">单价<br>（元）</td>
              <td width="450">供应区域</td>
            </tr>
            <tr v-for="(w, index) in l.workerVersions">
              <td :rowspan="l.workerVersions.length" v-if="index == 0">{{l.categoryName}}</td>
              <td :rowspan="l.workerVersions.length" v-if="index == 0">{{l.categoryId}}</td>
              <td :rowspan="l.workerVersions.length" v-if="index == 0">{{l.title}}</td>
              <td :rowspan="l.workerVersions.length" v-if="index == 0">{{l.unit}}</td>
              <td>{{w.version}}</td>
              <td>{{w.unitPrice}}</td>
              <td>
                <template v-for="(item, index) in w.area">
                  {{item}}
                </template>
              </td>
            </tr>
          </table>

          <dl class="etrf5">
            <input type="button" class="anniu33" value="编辑" @click="edit(l.id)">
            <input type="button" class="anniu34" value="删除" @click="remove(index, l.id)">
          </dl>
        </div>

        <!-- 模态框 -->
        <vue-msg ref="msg"></vue-msg>

        <dl class="ertvc"><input type="button" class="ybf18" value="新增工人定额" @click="add"></dl>
      </div>
    </div>
  </div>
</template>

<script>
  import VueMsg from '../../../common/components/VueMsg.vue'
  export default {
    name: 'quotaWorker',
    data () {
      return {
        list: [],
        msg: ''
      }
    }, mounted() {
      $.ajax({
        url: basePath + '/quota_worker/worker',
        type: 'get',
        dataType: 'json',
        success: (result) => {
          for (var i = 0; i < result.length; i++) {
            for (var j = 0; j < result[i].workerVersions.length; j++) {
              result[i].workerVersions[j].area = JSON.parse(result[i].workerVersions[j].area);
            }
          }
          this.list = result;
        }
      });
    }, methods: {
      add() {
        this.$router.push({path: '/add_quota_worker'});
      }, edit(id) {
        this.$router.push({path: '/edit_quota_worker/' + id});
      }, remove(index, id) {
        if (confirm('确定删除 ?')) {
          $.ajax({
            url: basePath + '/quota_worker/worker/' + id,
            type: 'post',
            dataType: 'json',
            data: {_method: 'delete'},
            success: (result) => {
              if (result.success) {
                this.list.splice(index, 1);
              } else {
                this.$refs.msg.show(result.msg);
              }
            }
          });
        }
      }
    }, components: {
      'vue-msg': VueMsg
    }
  }
</script>



