<template>
  <div id="right">
    <div class="r-main">
      <div class="fgb">
        <dl class="ujb">

          <dd class="unbv">装修模板</dd>
          <dd class="pil">
            <el-dropdown  trigger="click" @command="selectVersion">
                <span class="el-dropdown-link" style="cursor:pointer;width: 60px;">
                   V{{currVersion}}
                 <i class="el-icon-caret-bottom el-icon--right"></i>
                </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item :command=rule.version :key = rule.version v-for="(rule,key) in version">{{rule.version}}|{{rule.city}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </dd>

        </dl>

        <dl class="ujb">
          <dd class="unbv" >适用城市:</dd>
          <dd class="unbv" style="width: 40px;">{{rule.city}}</dd>

          <dd class="rtg">
            <router-link tag="input" type="button" value="编辑" class="anniu"
                         :to="{ name: 'fitRuleEdit', params:{version:currVersion }}">编辑
            </router-link>
          </dd>
          <dd class="gyhb1"><input type="button" class="umk" value="删除当前模板" v-on:click="delRule"></dd>
          <dd class="gyhb1">
            <router-link tag="input" type="button" value="新增装修模板" class="umk1" to="/fitRuleAdd">新增装修模板</router-link>
          </dd>
        </dl>
      </div>
    </div>

    <div class="dtg">
      <dl class="tyv">装修规则</dl>
      <div class="fxdc">
        <ul>
          <li v-for="(phase,key,index) in array">
            <div class="xun1">
              <div class="unf">
                <ul>
                  <li v-for="(task,index) in phase">
                    <dl class="ygt">
                      <dd>{{task.title}}</dd>
                      <dd>[*~*天]</dd>
                    </dl>
                  </li>
                </ul>
                <dl class="tfr">【第{{key}}阶段】</dl>
                <dl class="gtv"><input type="button" class="anniu1" value="编辑"></dl>
              </div>
              <dl class="fgv">
                <img src="../../../assets/images/ajian.jpg" width="35" height="36">
              </dl>
            </div>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>
<script>
  import axios from 'axios'
  import Vue from 'vue'
  import VM from '../../../common/js/bus.js'
  export default {
    data () {
      return {
        data: '',
        array: [],
        version: [],
        city: ['北京', '上海', '广州', '深圳'],
        currVersion: '1.0',
        currCity: '',
        rule: '',
        ruleId: '',
        flag: '0'
      }
    },
    mounted: function () {
      this.fetchVersionData()
    },
    watch: {
      currVersion: function () {
        this.fetchRuleData()
      }
    },
    methods: {
      fetchRuleData(){
        var url = basePath + "/fitRule/version/" + this.currVersion;
        axios.get(url).then(response => (
           this.handleData2(response)
        ))

      },
      handleData2(response){
          if(response.data!=null)
          {
          this.data = response.data,
          this.array = response.data.fitTaskList,
          this.rule = response.data.fitRule,
          this.currCity = this.rule.city,
          this.ruleId = this.rule.id,
          this.currVersion = this.rule.version
          }
        this.send()
      },
      fetchCityData(){
        var url = basePath + "/fitRule/cityList";
        axios.get(url
        ).then(response => (
          this.city = response.data
        ))
      },
      fetchVersionData(){
        var url = basePath + "/fitRule/versions";
        axios.get(url
        ).then(response => (
          this.handleData(response)
        ))
      },
      delRule(){
        if (confirm('确认删除吗？')) {
          var url = basePath + "/fitRule/deleteFitRuleBy";
          axios.get(url, {
              params: {
                id: this.ruleId
              }
            }
          ).then(response => (
            this.flag = response.data,
              this.fetchVersionData(),
              this.judge(this.flag)
          ))

        }

      },
      handleData(response){
        if (response.data != 0) {
          this.version = response.data,
          this.currVersion = this.version[0].version
        }
        else {
          this.$router.push({path:'fitRuleBlank'})
        }
      },
       send(){
        VM.bus.$emit('sendRule', this.data)
      },
      selectVersion(command){
        this.currVersion=command
      }
    }
  }
</script>



