<template>
  <div class="rtvmm">
    <div class="ygf">
      <div class="ertw">
        <dl class="trfv">
          <dd class="wrty">{{names.version}}<span class="font2">*</span></dd>
          <dd class="yuvvv"><input type="text" class="bgt" :name="'version_' + index" v-model="versions[index].version" v-validate="'required'"></dd>
        </dl>
        <span v-show="errors.has('version_' + index)" style="margin-left: 105px; color: red; font-size: xx-small">{{ errors.first('version_' + index) }}</span>
      </div>

      <div class="ertw">
        <dl class="trfv">
          <dd class="wrty">{{names.area}}<span class="font2">*</span></dd>
          <dd class="yuvvv">
            <select class="bgt1" :name="'status_' + index" v-model="status" @change="checkCity">
              <option value="-1">请选择</option>
              <option :value="index" v-for="(item, index) in citys">{{item}}</option>
            </select>
          </dd>
        </dl>
        <span v-show="versions[index].area.length == 0" style="margin-left: 105px; color: red; font-size: xx-small">{{ errors.first('status_' + index) }}</span>
      </div>
    </div>

    <div class="ertc">
      <dl class="erfbb">
        <dd class="rtfv">{{names.unitPrice}}<span class="font2">*</span></dd>
        <dd class="rtffd"><input type="text" class="bgt11" :name="'unitPrice_' + index" v-model="versions[index].unitPrice" v-validate="'required|decimal:2'"></dd>
      </dl>
      <span v-show="errors.has('unitPrice_' + index)" style="margin-left: 105px; color: red; font-size: xx-small">{{ errors.first('unitPrice_' + index) }}</span>
    </div>

    <div class="wrtvn">
      <dl class="ertvb" v-if="versions[index].area.length != 0">已选择：</dl>
      <template v-for="(item, index) in versions[index].area">
        <dl class="ung">{{item}}</dl>
        <dl class="ertvbm">
          <a href="javascript:void(0)"><img src="../../../assets/images/cha.jpg" width="22" height="22" @click="remove(index)"></a>
        </dl>
      </template>
    </div>
    <slot></slot>
  </div>
</template>

<script>
  import VM from '../../../common/js/bus.js'
  export default {
    name: 'version',
    props: ['names', 'versions', 'index'],
    data () {
      return {
        citys: ['北京', '上海', '广州', '深圳', '杭州'],
        status: -1
      }
    }, methods: {
      checkCity() {
        if (this.status != -1) {
          var value = this.citys[this.status];
          for (var i = 0; i < this.versions[this.index].area.length; i++) {
            if (value == this.versions[this.index].area[i]) {
              return;
            }
          }
          this.versions[this.index].area.push(this.citys[this.status]);
        } else {
          this.versions[this.index].area = [];
        }
        var areas = this.versions[this.index].area;

        if (areas.length != 0) {
          this.errors.remove("status_" + this.index);
        }
      }, remove(index) {
        this.versions[this.index].area.splice(index, 1);
        this.status = -1;
      }
    }, created() {
      VM.bus.$on('validate', () => {
        this.$validator.validateAll().then(() => {
        }).catch(() => {
        });

        var areas = this.versions[this.index].area;
        if (areas.length == 0) {
          this.errors.add('status_' + this.index, '请至少选择一个城市.', 'required', '__global__');
        }

        VM.bus.$emit('errors-changed', this.errors.errors);
      });
    }
  }
</script>



